.locationNotific {
  display: flex;
  justify-content: center;
  margin-top: 4vh;
  font-family: "Lucida Sans", sans-serif;
  color: #ff7b25;
  font-size: 14px;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2vh;
  margin-bottom: 0.5vh;
  font-size: 24px;
  font-family: "Lucida Sans", sans-serif;
  color: #034f84;
  height: 60px;
}

.title span::after {
  content: "";
  display: inline-block;
  width: 60px;
  height: 60px;
  background-image: url("../../img//cloudy.svg");
  background-size: contain;
}

.cityName {
  display: block;
  text-align: center;
  font-size: 20px;
  font-family: "Lucida Sans", sans-serif;
  color: #034f84;
  margin-top: 0.5vh;
  font-weight: 400;
}

.searchContainer {
  display: flex;
  align-items: center;
  text-align: center;
  margin-top: 1vh;
  flex-direction: column;
  font-size: 16px;
  font-family: "Lucida Sans", sans-serif;
  color: #034f84;
}

@media (min-width: 980px) {
  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2vh;
    margin-bottom: 0.5vh;
    font-size: 28px;
    font-family: "Lucida Sans", sans-serif;
    color: #034f84;
    height: 80px;
  }

  .title span::after {
    content: "";
    display: inline-block;
    width: 80px;
    height: 80px;
    background-image: url("../../img//cloudy.svg");
    background-size: contain;
  }
  .searchContainer {
    display: flex;
    align-items: center;
    margin-top: 1vh;
    flex-direction: column;
    font-size: 20px;
    font-family: "Lucida Sans", sans-serif;
    color: #034f84;
  }
}
